let currentAudio = null;

export function playAudio(src) {
  if (currentAudio) {
    currentAudio.pause();
    currentAudio.currentTime = 0;
  }
  
  currentAudio = new Audio(src);
  currentAudio.play().catch(error => console.error('音频播放失败:', error));
  
  currentAudio.onended = () => {
    currentAudio = null;
  };
}