import React, { useState, useEffect, useCallback } from 'react';
import { DndProvider, useDrag, useDrop, useDragLayer } from 'react-dnd';
import { HTML5Backend, getEmptyImage } from 'react-dnd-html5-backend';
import { TouchBackend } from 'react-dnd-touch-backend';
import './SingleChoiceDrag.css';
import config from './SingleChoiceDragConfig';
import { useGifAnimation } from './CenteredGif';

// 检测是否为触摸设备
const isTouchDevice = 'ontouchstart' in window;

// 可拖动项组件
function DraggableItem({ item, type, isDropped }) {
  // 使用useDrag钩子设置拖动逻辑
  const [{ isDragging }, drag, preview] = useDrag(() => ({
    type: type,
    item: { id: item.id, type: type, imageUrl: item.imageUrl },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    })
  }));

  // 使用空白图像作为拖动预览,以便自定义预览
  useEffect(() => {
    preview(getEmptyImage(), { captureDraggingState: true });
  }, [preview]);

  // 渲染可拖动项
  return (
    <div
      ref={drag}
      className={`draggable-item ${isDragging ? 'dragging' : ''} ${isDropped ? 'dropped' : ''}`}
    >
      <img src={item.imageUrl} alt={item.content} />
    </div>
  );
}

// 拖动预览组件
function DragPreview() {
  // 使用useDragLayer钩子获取拖动状态
  const { isDragging, item, currentOffset } = useDragLayer((monitor) => ({
    item: monitor.getItem(),
    currentOffset: monitor.getSourceClientOffset(),
    isDragging: monitor.isDragging(),
  }));

  // 计算拖动预览的样式
  const getItemStyles = useCallback((currentOffset) => {
    if (!currentOffset) {
      return { display: 'none' };
    }

    const { x, y } = currentOffset;
    const transform = `translate(${x}px, ${y}px)`;
    return {
      transform,
      WebkitTransform: transform,
    };
  }, []);

  // 渲染拖动预览
  if (!isDragging) {
    return null;
  }

  return (
    <div className="drag-layer">
      <div className="drag-preview" style={getItemStyles(currentOffset)}>
        <img src={item.imageUrl} alt="拖动预览" />
      </div>
    </div>
  );
}

// 放置目标组件
function DropTarget({ onDrop, children, droppedItem }) {
  // 使用useDrop钩子设置放置逻辑
  const [{ isOver }, drop] = useDrop(() => ({
    accept: 'item', // 指定接受的拖动类型
    drop: (item) => onDrop(item.id), // 处理放置事件
    collect: (monitor) => ({
      isOver: !!monitor.isOver(), // 检测是否有项目悬停在放置区域上
    }),
  }));

  // 渲染放置区域
  return (
    <div ref={drop} className={`drop-area ${isOver ? 'is-over' : ''}`}>
      {children}
      {droppedItem && (
        <div className="dropped-item">
          <img src={droppedItem.imageUrl} alt={droppedItem.content} />
        </div>
      )}
    </div>
  );
}

// 主组件
function SingleChoiceDrag() {
  // 设置状态
  const [items] = useState(config.items);
  const [result, setResult] = useState(null);
  const [droppedItem, setDroppedItem] = useState(null);

  const { GifComponent, playGif } = useGifAnimation();

  // 处理放置事件
  const handleDrop = (itemId) => {
    const item = items.find(i => i.id === itemId);
    if (itemId === config.correctAnswerId) {
      setDroppedItem(item);
      setResult(config.successMessage);
      playGif('https://wimg.588ku.com/gif320/24/07/09/0840308e105e461c930b79b7acac9582.gif'); // 播放成功动画
    } else {
      setResult(config.failureMessage);
      setDroppedItem(null);
      playGif('/path/to/failure.gif'); // 播放失败动画
    }
  };

  // 渲染主组件
  return (
    <DndProvider backend={isTouchDevice ? TouchBackend : HTML5Backend}>
      <div className="single-choice-drag" style={{ position: 'relative' }}>
        {/* <h2>{config.title}</h2> */}
        <DropTarget onDrop={handleDrop} droppedItem={droppedItem}>
          <div className="drop-area-content">
            <img src={config.dropAreaImage} alt={config.dropAreaAltText} />
          </div>
        </DropTarget>
        <div className="items-container">
          {items.map((item) => (
            <DraggableItem 
              key={item.id} 
              item={item} 
              type="item" 
              isDropped={droppedItem && droppedItem.id === item.id}
            />
          ))}
        </div>
        {result && <div className="result">{result}</div>}
        <DragPreview />
        {GifComponent} {/* 添加GIF组件 */}
      </div>
    </DndProvider>
  );
}

export default SingleChoiceDrag;
