import React, { useState } from 'react';

const CenteredGif = ({ gifSrc, onAnimationEnd }) => {
  return (
    <div style={{
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      zIndex: 9999,
      backgroundColor: 'rgba(0, 0, 0, 0.5)'
    }}>
      <img 
        src={gifSrc} 
        alt="动画" 
        onLoad={(e) => {
          if (e.target.complete) {
            // setTimeout(onAnimationEnd, e.target.duration * 1000);
            setTimeout(onAnimationEnd,2 * 1000);
          }
        }}
      />
    </div>
  );
};

export const useGifAnimation = () => {
  const [gifSrc, setGifSrc] = useState(null);

  const playGif = (src) => {
    setGifSrc(src);
  };

  const hideGif = () => {
    setGifSrc(null);
  };

  return {
    GifComponent: gifSrc ? <CenteredGif gifSrc={gifSrc} onAnimationEnd={hideGif} /> : null,
    playGif
  };
};

