import React, { useState, useRef } from 'react';

const AudioButton = ({ audioSrc, imageSrc, altText = '播放音频' }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const audioRef = useRef(null);

  const playAudio = () => {
    const audio = audioRef.current;
    audio.currentTime = 0; // 重置音频到开始位置
    audio.play();
    setIsPlaying(true);
  };

  const handleClick = () => {
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      playAudio();
    }
  };

  return (
    <div>
      <button onClick={handleClick}>
        {imageSrc ? (
          <img src={imageSrc} alt={altText} style={{ width: '50px', height: '50px' }} />
        ) : (
          altText
        )}
      </button>
      <audio 
        ref={audioRef} 
        src={audioSrc} 
        onEnded={() => setIsPlaying(false)}
      />
    </div>
  );
};

export default AudioButton;

