import React, { useState, useEffect } from 'react';
import './GameRenderer.css';
import { playAudio } from '../utils';

const GameElement = ({ element, onAddElement, onUpdateElement, onRemoveElement }) => {
  const { resource, lefts, tops, levels, event, clicks } = element;
  const style = {
    position: 'absolute',
    left: `${lefts}px`,
    top: `${tops}px`,
    zIndex: levels,
    width: `${resource.withs}px`,
    height: `${resource.highs}px`,
    id: `element-${element.id}`, // 添加 id 属性
  };

  const handleClick = () => {
    if (event === 1 && clicks) {
      console.log(`点击了元素: ${resource.names}`);
      clicks.forEach(clickElement => {
        switch (clickElement.rtype) {
          case 2: // GIF
            const gifElement = {
              ...clickElement,
              id: `temp-gif-${Date.now()}`, // 为临时GIF创建唯一ID
              event: 0, // 禁用点击事件
            };
            onAddElement(gifElement);
            setTimeout(() => {
              onRemoveElement(gifElement.id);
            }, clickElement.resource.duration * 1000);
            break;
          case 3: // 音频
            playAudio(clickElement.resource.path);
            break;
          case 1: // 图片
            if (onAddElement && onUpdateElement) {
              const existingElement = document.getElementById(`element-${clickElement.id}`);
              if (existingElement) {
                onUpdateElement(clickElement);
              } else {
                onAddElement(clickElement);
              }
            }
            break;
          default:
            break;
        }
      });
    }
  };

  switch (resource.rtype) {
    case 1: // 图片
    case 5: // 底图
      return <img id={`element-${element.id}`} src={resource.path} alt={resource.names} style={style} onClick={handleClick} />;
    case 2: // GIF
      return <img id={`element-${element.id}`} src={resource.path} alt={resource.names} style={style} onClick={handleClick} />;
    case 3: // 音频
      return <audio id={`element-${element.id}`} src={resource.path} style={style} controls />;
    case 4: // 文本
      const textStyle = {
        ...style,
        backgroundColor: `rgb(${resource.backgroundcolor})`,
        borderRadius: `${resource.borderradius}px`,
        fontSize: `${resource.fontsize}px`,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        textAlign: 'center',
      };
      return <div id={`element-${element.id}`} style={textStyle} onClick={handleClick}>{resource.names}</div>;
    default:
      return null;
  }
};

const GameRenderer = ({ config }) => {
  const [elements, setElements] = useState(config.elements);

  const handleAddElement = (newElement) => {
    setElements(prevElements => [...prevElements, newElement]);
  };

  const handleUpdateElement = (updatedElement) => {
    setElements(prevElements => prevElements.map(el => 
      el.id === updatedElement.id ? updatedElement : el
    ));
  };

  const handleRemoveElement = (elementId) => {
    setElements(prevElements => prevElements.filter(el => el.id !== elementId));
  };

  return (
    <div className="game-container">
      {elements.map(element => (
        <GameElement 
          key={element.id} 
          element={element} 
          onAddElement={handleAddElement}
          onUpdateElement={handleUpdateElement}
          onRemoveElement={handleRemoveElement}
        />
      ))}
    </div>
  );
};

export default GameRenderer;
