import React, { useState, useEffect, useRef } from 'react';
import { Routes, Route } from 'react-router-dom';
import SingleChoiceDrag from '../components/SingleChoiceDrag';
import MultiChoiceDrag from '../components/MultiChoiceDrag';
import ColoringGame from '../components/ColoringGame';
import Home from './Home';
import GameRenderer from '../components/GameRenderer';
const GameCenter = () => {
  const config = {
    withs: 700,
    highs: 400,

    "elements": [
      {
        "id": 1,
        "rtype": 1,
        "resource": {
          "rtype": 1,
          "names": "背景图片",
          "withs": 800,
          "highs": 400,
          "path": "https://picsum.photos/800/400"
        },
        "lefts": 0,
        "tops": 0,
        "levels": 0,
        "event": null
      },
      {
        "id": 2,
        "rtype": 4,
        "resource": {
          "rtype": 4,
          "names": "欢迎",
          "withs": 200,
          "highs": 50,
          "backgroundcolor": "255,255,255",
          "borderradius": 10,
          "fontsize": 18
        },
        "lefts": 300,
        "tops": 50,
        "levels": 1,
        "event": 1,
        "clicks": [
          {
            "id": 1,
            "rtype": 2,
            "resource": {
              "rtype": 2,
              "names": "动画按钮",
              "withs": 100,
              "highs": 100,
              "path": "https://media.giphy.com/media/3o7ZeQBhbVGnELP4bK/giphy.gif",
              "duration": 5
            },
            "lefts": 200,
            "tops": 200,
            "levels": 1,
          },
          {
            "id": 2,
            "rtype": 3,
            "resource": {
              "rtype": 3,
              "names": "背景音乐",
              "path": "https://www.soundhelix.com/examples/mp3/SoundHelix-Song-1.mp3",
              "duration": 60
            },
            "lefts": 50,
            "tops": 50,
            "levels": 0,
          },
          {
            "id": 3,
            "rtype": 1,
            "resource": {
              "rtype": 1,
              "names": "静态图片",
              "withs": 200,
              "highs": 150,
              "path": "https://picsum.photos/200/150"
            },
            "lefts": 400,
            "tops": 300,
            "levels": 2,
          }
        ]
      },
      {
        "id": 3,
        "rtype": 2,
        "resource": {
          "rtype": 2,
          "names": "动画效果",
          "withs": 100,
          "highs": 100,
          "path": "https://media.giphy.com/media/3o7ZeQBhbVGnELP4bK/giphy.gif",
          "duration": 5
        },
        "lefts": 150,
        "tops": 200,
        "levels": 2,
        "event": null
      }
    ]
  };

  const [scale, setScale] = useState(1);
  const gameCenterRef = useRef(null);

  useEffect(() => {
    const handleResize = () => {
      if (gameCenterRef.current && config.withs && config.highs) {
        const { clientWidth, clientHeight } = document.documentElement;
        const scaleX = clientWidth / config.withs;
        const scaleY = clientHeight / config.highs;
        const newScale = Math.min(scaleX, scaleY);
        setScale(newScale);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [config.withs, config.highs]);

  return (
    <div
      ref={gameCenterRef}
      style={{
        width: `${config.withs}px`,
        height: `${config.highs}px`,
        transform: `scale(${scale})`,
        transformOrigin: 'top left',
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: `-${config.highs / 2}px`,
        marginLeft: `-${config.withs / 2}px`
      }}
    >
      <Routes>
        <Route path="/" element={<Home config={config} />} />
        <Route path="/singleChoice" element={<SingleChoiceDrag config={config} />} />
        <Route path="/multiChoice" element={<MultiChoiceDrag config={config} />} />
        <Route path="/coloring" element={<ColoringGame config={config} />} />
        <Route path="/test" element={<GameRenderer config={config} />} />
      </Routes>
    </div>
  );
};

export default GameCenter;
