import React, { useState, useEffect, useCallback } from 'react';
import { DndProvider, useDrag, useDrop, useDragLayer } from 'react-dnd';
import { HTML5Backend, getEmptyImage } from 'react-dnd-html5-backend';
import { TouchBackend } from 'react-dnd-touch-backend';
import './MultiChoiceDrag.css';
import { useGifAnimation } from './CenteredGif';

// 拖拽游戏的规则
// 有多个目标区域，多个拖拽区域，每个拖拽区域有一个可拖拽项
// 拖拽项可以放置在目标区域中，如果拖拽项和目标区域类型相同，则拖拽项停留在目标区域，原位置的拖拽项消失，否则拖拽项会回到原位置
// 所有目标区域都被填充后，弹出成功动画


const config = {
  "title": "多选拖拽题",
  // 拖拽区域
  "draggableitems": [
    { "id": 1, "type": "A", "content": "海豚", "imageUrl": require('../images/a1.png') },
    { "id": 2, "type": "A", "content": "鱼", "imageUrl": require('../images/a2.png') },
    { "id": 4, "type": "B", "content": "马", "imageUrl": require('../images/b1.png') },
    { "id": 5, "type": "B", "content": "羊", "imageUrl": require('../images/b2.png') },
  ],
  // 目标区域
  "droptargets": [
    { "id": 1, "type": "A", "content": "水中动物", "imageUrl": require('../images/a.png') },
    { "id": 2, "type": "A", "content": "水中动物", "imageUrl": require('../images/a.png') },
    { "id": 3, "type": "B", "content": "陆地动物", "imageUrl": require('../images/b.png') },
    { "id": 4, "type": "B", "content": "陆地动物", "imageUrl": require('../images/b.png') }
  ],
  // 过关动画
  success: {
    gifUrl: 'https://wimg.588ku.com/gif320/24/07/09/0840308e105e461c930b79b7acac9582.gif',
    duration: 2,
  },
  // 失败动画
  failure: {
    gifUrl: 'https://wimg.588ku.com/gif320/24/07/09/0840308e105e461c930b79b7acac9582.gif',
    duration: 2,
  }
};
// 检测是否为触摸设备
const isTouchDevice = 'ontouchstart' in window;

// 可拖动项组件
function DraggableItem({ item }) {
  // 使用useDrag钩子设置拖动逻辑
  const [{ isDragging }, dragRef, preview] = useDrag(() => ({
    type: item.type,
    item: { id: item.id, type: item.type, imageUrl: item.imageUrl },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    })
  }));

  // 使用空白图像为拖动预览,以便自定义预览
  useEffect(() => {
    preview(getEmptyImage(), { captureDraggingState: true });
  }, [preview]);

  // 渲染可拖动项
  return (
    <div
      ref={dragRef}
      className={`draggable-item ${isDragging ? 'dragging' : ''} ${item.isDropped ? 'dropped' : ''}`}
    >
      <img src={item.imageUrl} alt={item.content} />
    </div>
  );
}

// 拖动预览组件
function DragPreview() {
  // 使用useDragLayer钩子获取拖动状态
  const { isDragging, item, currentOffset } = useDragLayer((monitor) => ({
    item: monitor.getItem(),
    currentOffset: monitor.getSourceClientOffset(),
    isDragging: monitor.isDragging(),
  }));

  // 计算拖动预览的样式
  const getItemStyles = useCallback((currentOffset) => {
    if (!currentOffset) {
      return { display: 'none' };
    }

    const { x, y } = currentOffset;
    const transform = `translate(${x}px, ${y}px)`;
    return {
      transform,
      WebkitTransform: transform,
    };
  }, []);

  // 渲染拖动预览
  if (!isDragging) {
    return null;
  }

  return (
    <div className="drag-layer">
      <div className="drag-preview" style={getItemStyles(currentOffset)}>
        <img src={item.imageUrl} alt="拖动预览" />
      </div>
    </div>
  );
}

// 放置目标组件
function DropTarget({ onDrop, children, target }) {
  // 使用useDrop钩子设置放置逻辑
  const [{ isOver }, dropRef] = useDrop(() => ({
    accept: target.type, // 指定接受的拖动类型
    drop: onDrop, // 处理放置事件
    collect: (monitor) => ({
      isOver: !!monitor.isOver(), // 检测是否有项目悬停在放置区域上
    }),
  }));

  // 渲染放置区域
  return (
    <div ref={dropRef} className={`drop-area ${isOver ? 'is-over' : ''}`}>
      <div className="drop-area-content">
        <div className="target">
          <img src={target.imageUrl} alt={target.content} />
          <p>{target.content}</p>
        </div>
        {/* 渲染被拖拽成功的 item */}
        {target.item && (
          <div className="dropped-item">
            <img src={target.item.imageUrl} alt={target.item.content} />
            {/* <p>{target.item.content}</p> */}
          </div>
        )}
      </div>

      {children}
    </div>
  );
}

// 主组件
function MultiChoiceDrag() {
  // 设置状态
  const [items, setItems] = useState(config.draggableitems.map(item => ({
    ...item,
    isDropped: false
  })));

  const [droppedItems, setDroppedItems] = useState(
    config.droptargets.map(target => ({
      ...target,
      item: null // 初始化时没有放置的项目
    }))
  );

  const { GifComponent, playGif } = useGifAnimation();

  // 处理放置事件
  const handleDrop = (item, target) => {
    console.log('item', item);
    console.log('target', target);
    if (!target.item && item.type === target.type) {
      setDroppedItems((prevItems) =>
        prevItems.map(t =>
          t.id === target.id ? { ...t, item } : t
        )
      );
      setItems((prevItems) => prevItems.map(i =>
        i.id === item.id ? { ...i, isDropped: true } : i
      )); // 更新isDropped状态
    } else {
      // 如果放置错误，播放失败动画
      playGif(config.failure.gifUrl);
    }
  };

  useEffect(() => {
    // 检查是否所有正确答案都已放置
    const allCorrect = droppedItems.every(t => t.item);
    console.log('droppedItems', droppedItems);
    console.log('allCorrect', allCorrect);
    if (allCorrect) {
      playGif(config.success.gifUrl); // 播放成功动画
    }
    console.log('droppedItems updated', droppedItems);
  }, [droppedItems, playGif]);

  // 渲染主组件
  return (
    <DndProvider backend={isTouchDevice ? TouchBackend : HTML5Backend}>
      <div className="multi-choice-drag" style={{ position: 'relative' }}>
        <div className="drop-targets-container">
          {droppedItems.map(target => (
            <DropTarget
              key={target.id}
              onDrop={(item) => handleDrop(item, target)}
              target={target}
            />
          ))}
        </div>
        <div className="items-container">
          {items.map((item) => (
            <DraggableItem
              key={item.id}
              item={item}
            />
          ))}
        </div>
        <DragPreview />
        {GifComponent} {/* 添加GIF组件 */}
      </div>
    </DndProvider>
  );
}

export default MultiChoiceDrag;
